.slick-arrow {
  position: absolute;
  height: 50px;
  width: 50px;
  top: 50%;
  border: 1px solid $border-color;
  background: $color-white;
  color: $grey-soft;
  text-align: center;
  z-index: 1;
  font-size: 14px;
  line-height: 50px;

  @include transform(translateY(-50%));
  @include border-radius(50%);
}

// slick-nav
.slick-nav {
  .slick-prev {
    left: -15px;
    opacity: 0;
    visibility: hidden;

    @include min-screen(1500) {
      left: -40px;
    }
  }

  &:hover {
    .slick-prev {
      left: -15px;
      opacity: 1;
      visibility: visible;

      @include max-screen(1480) {
        left: 0;
      }
    }
  }

  .slick-next {
    right: -15px;
    opacity: 0;
    visibility: hidden;

    @include min-screen(1500) {
      right: -40px;
    }
  }

  &:hover {
    .slick-next {
      right: -15px;
      visibility: visible;
      opacity: 1;

      @include max-screen(1480) {
        right: 0;
      }
    }
  }
}

// slick-nav-sync
.slick-nav-sync {
  .slick-prev {
    left: -15px;
    opacity: 0;
    visibility: hidden;
  }

  &:hover {
    .slick-prev {
      left: 15px;
      opacity: 1;
      visibility: visible;
    }
  }

  .slick-next {
    right: -15px;
    opacity: 0;
    visibility: hidden;
  }

  &:hover {
    .slick-next {
      right: 15px;
      visibility: visible;
      opacity: 1;
    }
  }
}

// slick-nav-brand
.slick-nav-brand {
  .slick-prev {
    left: 0;
    opacity: 0;
    visibility: hidden;
  }

  &:hover {
    .slick-prev {
      left: -40px;
      opacity: 1;
      visibility: visible;

      @include max-screen(1480) {
        left: -10px;
      }
    }
  }

  .slick-next {
    right: 0;
    opacity: 0;
    visibility: hidden;
  }

  &:hover {
    .slick-next {
      right: -40px;
      visibility: visible;
      opacity: 1;

      @include max-screen(1480) {
        right: -10px;
      }
    }
  }
}

// blog-init
.blog-init {
  .slick-prev {
    left: 0;
