/* slick progress */

.slick-progress {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 5px;
  z-index: 1;

  span {
    width: 0;
    height: 100%;
    position: absolute;
    background-color: $progress-color;

    @include transition(0s linear);

    &.active {
      width: 100%;
    }
  }
}

/* slick dots style */
.dots-style {
  .slick-dots {
    position: absolute;
    left: 0;
    bottom: 30px;
    width: 100%;
    display: flex !important;
    justify-content: center;
    align-items: center;

    li {
      line-height: 1;

      button {
        width: 14px;
        height: 14px;
        text-indent: 2000px;
        font-size: 0;
        display: block;
        margin: 0 5px;

        @include transition(0.3s);
        @include border-radius(50%);

        &:hover {
          @include transform(scale3d(1, 1, 1));
        }
      }
    }
  }
}

